<template>
	<div class="header">
		<img src="../assets/head.jpg" alt="">
		<h2>{{title}}</h2>
	</div>
</template>
<script>
	export default{
		data(){
			return{
				title:"Welcome to SunnyTang'home"
			}
		}
	}
</script>
<style scoped>
	.header{
		/*z-index: 100;*/
		font-size: 62.5%;
		position: relative;
		width:100%;
		text-align: center;
	}
	.header img{
		width:100%;
		position: fixed;
		top:0;
		left:0;
	}
	.header h2{
		position:fixed;
		left:5%;
		top:5%;
		font-size:1.5rem;
		color:#4747a5;
	}
</style>


