<template>
	<div class="mine-page">
		<div class="wrap">
			<h3>{{title}}</h3>
			<div class="first">
				<h5>食物金字塔</h5>
				<img src="../assets/mine/food pyramid.jpg" alt="">
				<ul>
					<li v-for='item in pyramid'>{{item.head}}
						<p v-for='(detail,index) in item.detail'>{{index+1}}、{{detail.list}}</p>
					</li>					
				</ul>
			</div>	
		</div>
	</div>
</template>
<script>
	export default{
		data(){
			return{
				'title':'闲来无聊，研究研究好吃的，吼吼！',
				'pyramid':[
					{
						'head':'食物多样，谷类为主',	
						'detail':[
							{
								'list':'每天的膳食应包括谷薯类、蔬菜水果类、畜禽鱼蛋奶类、大豆坚果类等食物。'
							},
							{
								'list':'平均每天摄入12种以上食物，每周25种以上。'
							},
							{
								'list':'每天摄入谷薯类食物250-400g，其中全谷物和杂豆类50-150g，薯类50-100g。'	
							},
							{
								'list':'食物多样、谷类为主是平衡膳食模式的重要特征。'
							}
						]		
					},
					{
						'head':'多吃蔬果、奶类、大豆',	
						'detail':[
							{
								'list':'蔬菜水果是平衡膳食的重要组成部分，奶类富含钙，大豆富含优质蛋白质。'
							},
							{
								'list':'餐餐有蔬菜，保证每天摄入300-500g蔬菜，深色蔬菜应占1/2。'
							},
							{
								'list':'每日吃水果，保证每天摄入200-350g新鲜水果，果汁不能代替鲜果。'	
							},
							{
								'list':'吃各种各样的奶制品，相当于每天液态奶300g。经常吃豆制品，适量吃坚果。'
							}
						]		
					},
					{
						'head':'适量吃鱼、禽、蛋、瘦肉',	
						'detail':[
							{
								'list':'鱼、禽、蛋和瘦肉摄入要适量。'
							},
							{
								'list':'每周吃鱼280-525g，畜禽肉280-525g，蛋类280-350g，平均每天摄入总量120-200g。'
							},
							{
								'list':'优先选择鱼和禽。吃鸡蛋不弃蛋黄。少吃肥肉、烟熏和腌制肉制品。'	
							}
						]		
					},
					{
						'head':'少盐少油，控糖限酒',	
						'detail':[
							{
								'list':'培养清淡饮食习惯，少吃高盐和油炸食品。成人每天食盐不超过6g，每天烹调油25~30g。'
							},
							{
								'list':'控制添加糖的摄入量，每天摄入不超过50g，最好控制在25g以下。每日反式脂肪酸摄入量不超过2g。'
							},
							{
								'list':'足量饮水，成年人每天7-8杯（1500-1700ml），提倡饮用白开水和茶水；不喝或少喝含糖饮料。儿童少年、孕妇、乳母不应饮酒。成人如饮酒，男性一天饮用酒的酒精量不超过25g，女性不超过15g。'	
							}
						]		
					}
				]
			}
		}
	}
</script>
<style scoped>
	img{
		width:100%;
	}
	ul li{
		font-size: 1rem;
		color:#200dc2;
		margin-top:.6rem;
	}
	p{
		margin-top: .7rem;
		font-size: .9rem;
		color:#555;
	}
	.mine-page{
		width:100%;
	}
	.mine-page .wrap{
		width:96%;
		margin:15rem auto 5rem;
	}
	.mine-page .wrap h3{
		font-size:1.3rem;
		color:#1e6e6d;
	}
	.mine-page .wrap .first{
		width:100%;
		height: 100%;
		margin-top:1.3rem;
		text-align: left;
	}
	.mine-page .wrap h5{
		font-size: 1rem;
		color:#666;
		margin-bottom: 1rem;
	}

</style>


