<template>
	<div class="index-page">
		<div class="wrap">
			<!-- <div class="circle"></div> -->
			<div>
				<h3>{{title}}</h3>
			</div>
			<div class="heart"></div>
			<div class="heart second"></div>
			<div class="heart third"></div>
			<div class="heart forth"></div>
			<div class="heart fifth"></div>
			<div class="heart sixth"></div>
			<div class="heart seventh"></div>
			<div class="heart eighth"></div>
			<div class="heart nineth"></div>
			<div class="heart tenth"></div>
		</div>
	</div>
</template>
<script>
	export default{
		data(){
			return{
				title:'终于等到你！！！'
			}
		}
	}
</script>
<style scoped>
	.index-page{
		width:100%;
		height:400px;
	}
	.index-page .wrap{
		width:96%;
		height:100%;
		margin:15rem auto 0;
	}
	/*.index-page .wrap .circle{
		position: absolute;
		left: 0;
		bottom:3rem;
		height: 3rem;
		width:3rem;
		background:#161cc0;
		border-radius: 50%;
		border:1px solid pink;
		animation: up 3s ease-out infinite;
	}*/
	.index-page .wrap div:first-child{
		position: relative;
	}
	.index-page .wrap h3{
		font-size: 2rem;
		color:#3743cc;
		animation: welcome 3.8s linear ;
	}

	.index-page .wrap .heart{
		position:absolute;
		left:5%;
		top:46%;
		width:11rem;
		height:10rem;
		animation: big 3s linear infinite;	
	}
	.index-page .wrap .heart.second{
		position: absolute;
		left:50%;
		top:48%;
	}
	.index-page .wrap .heart.third{
		position: absolute;
		left:3%;
		top:50%;
		animation: down 3.2s ease-out infinite;
	}
	.index-page .wrap .heart.forth{
		position: absolute;
		left:43%;
		top:49%;
		animation: up 3.4s ease-out infinite;
	}
	.index-page .wrap .heart.fifth{
		position: absolute;
		left:38%;
		top:30%;
	}
	.index-page .wrap .heart.sixth{
		position: absolute;
		left:25%;
		top:36%;
	}
	.index-page .wrap .heart.seventh{
		position: absolute;
		left:10%;
		top:58%;
	}
	.index-page .wrap .heart.eighth{
		position: absolute;
		left:20%;
		top:40%;
		animation: up 3.2s ease-out infinite;
	}
	.index-page .wrap .heart.ninth{
		position: absolute;
		left:15%;
		top:50%;
		animation: up 3.8s ease-out infinite;
	}
	.index-page .wrap .heart.tenth{
		position: absolute;
		left:8%;
		top:36%;
		animation: up 4.2s ease-out infinite;
	}
	.index-page .wrap .heart::before{
		content:'';
		border-radius:5rem 5rem 0 0;
		background:#ed7ce5;
		width:5rem;
		height:9rem;
		transform:rotate(-45deg); 
		-webkit-transform:rotate(-45deg);
		-moz-transform:rotate(-45deg);
		-o-transform:rotate(-45deg);
		-ms-transform:rotate(-45deg);
		position: absolute;
		left:1.5rem;
	}
	.index-page .wrap .heart::after{
		content:'';
		border-radius:5rem 5rem 0 0;
		background: #ed7ce5;
		width:5rem;
		height:9rem;
		transform:rotate(45deg); 
		-webkit-transform:rotate(45deg);
		-moz-transform:rotate(45deg);
		-o-transform:rotate(45deg);
		-ms-transform:rotate(45deg);
		position: absolute;
		left:4.4rem;
	}
	@keyframes welcome{
		0%{
			opacity:0.0;
		}
		30%{
			opacity: 0.3;
		}
		65%{
			opacity: 0.7;
		}
		100%{
			opacity: 1;
		}
	}
	@keyframes big{
		0%{
			transform: scale(0);
			opacity:0.0;
		}
		25%{
			transform: scale(0.2);
			opacity: 0.1;
		}
		50%{
			transform: scale(0.4);
			opacity: 0.3;
		}
		75%{
			transform: scale(0.6);
			opacity: 0.5;
		}
		100%{
			transform: scale(0.8);
			opacity: 0.0;
		}
	}
	@keyframes up{
		0%{
			transform: scale(0) translateY(40rem);
			opacity:0.0;
		}
		20%{
			transform: scale(0.3) translateY(20rem);
			opacity: 0.1;
		}
		40%{
			transform: scale(0.6) translateY(0rem);
			opacity: 0.3;
		}
		60%{
			transform: scale(1) translateY(-13rem);
			opacity: 0.5;
		}
		80%{
			transform: scale(1.5) translateY(-26rem);
			opacity: 0.5;
		}
		100%{
			transform: scale(2) translateY(-40rem);
			opacity: 0.0;
		}
	}
	@keyframes down{
		0%{
			transform: scale(0) translateY(-20rem);
			opacity:0.0;
		}
		25%{
			transform: scale(0.3) translateY(0rem);
			opacity: 0.1;
		}
		50%{
			transform: scale(0.6) translateY(10rem);
			opacity: 0.3;
		}
		75%{
			transform: scale(1) translateY(25rem);
			opacity: 0.5;
		}
		100%{
			transform: scale(1.5) translateY(40rem);
			opacity: 0.5;
		}
	}
</style>


